<template>
  <div>
    <blockquote class="explain-txt"><a href="http://www.layui.com/laydate/">LayDate</a></blockquote>
    <blockquote class="explain-txt"></blockquote>
    <module-datePicker @change="handleSelectDate"></module-datePicker>
    <h3 class="explain-h3">Date Attributes</h3>
    <table class="explain-table">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>date</td>
          <td></td>
          <td>Date</td>
          <td></td>
          <td>new Date()</td>
        </tr>
        <tr>
          <td>format</td>
          <td></td>
          <td>String</td>
          <td>'yyyy-mm-dd','yyyy/mm/dd'</td>
          <td>'yyyy-mm-dd'</td>
        </tr>
      </tbody>
    </table>
    <h3 class="explain-h3">Date Events</h3>
    <table class="explain-table">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>change</td>
          <td></td>
          <td> value </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script type="text/javascript">
import DatePicker from '@/components/datePicker/datePicker';
export default {
  'name': 'moduleDatePicker',
  'components': {
    'module-datePicker': DatePicker
  },
  'methods':{
  	handleSelectDate(value){
        alert(value);
    }
  }
};

</script>



