var _utils = {
	//
	//14
	//2
	//3400
	//1212
	//12332
	'isLeapYear':function(year){
		var a = year % 4 === 0 && year % 100 !== 0;
		var b = year % 400 === 0;

		if (a || b) {
			return true;
		} else {
			return false;
		}
	},
	//
	'addZero':function(num){
		var a = num >= 0 && num < 10;

		return a ? '0' + num : num;
	},
	//
	'formatDate':function(year,month,day,format){
		if (/\b(m|M){2}\b/.test(format) && /\b(d|D){2}\b/.test(format)) {
			month = this.addZero(month);
			day = this.addZero(day);
		}

		var _date = '';
		var _arry = [];

		if (/^(y|Y){1,4}-(m|M){1,2}-(d|D){1,2}$/.test(format)) {
			_arry = [year,month,day];

			_date = _arry.join('-');
		} else if (/^(d|D){1,2}-(m|M){1,2}-(y|Y){1,4}$/.test(format)){
			_arry = [day,month,year];

			_date = _arry.join('-');
		} else if (/^(y|Y){1,4}\/(m|M){1,2}\/(d|D){1,2}$/.test(format)){
			_arry = [year,month,day];

			_date = _arry.join('/');
		} else if (/^(d|D){1,2}\/(m|M){1,2}\/(y|Y){1,4}$/.test(format)){
			_arry = [day,month,year];

			_date = _arry.join('/');
		} else {
			_date = '';
		}

		return _date;
	},
	//
	'cloneObj':function(oldObj){
		if (typeof oldObj !== 'object') {
			return oldObj;
		}

		if (oldObj === null) {
			return oldObj;
		}
		var newObj = Object();

		for (var i in oldObj){
			newObj[i] = this.cloneObj(oldObj[i]);
		}
		return newObj;
	},
	//
	'extendObj':function(){
		var args = arguments;//args

		if (args.length < 2) {
			return;
		}
		var temp = this.cloneObj(args[0]); //

		for (var n = 1; n < args.length; n++) {
			for (var i in args[n]) {
				temp[i] = args[n][i];
			}
		}
		return temp;
	}
};

export default _utils;


